import React, { useEffect, useState } from 'react';
import { Button, Snackbar } from '@material-ui/core';

function InstallPWA() {
    const [supportsPWA, setSupportsPWA] = useState(false);
    const [promptInstall, setPromptInstall] = useState(null);
    const [showInstallMessage, setShowInstallMessage] = useState(false);
    const [isStandalone, setIsStandalone] = useState(false);

    useEffect(() => {
        // Check if app is already installed
        const checkStandalone = () => {
            const isInStandaloneMode = window.matchMedia('(display-mode: standalone)').matches 
                || (window.navigator.standalone) 
                || document.referrer.includes('android-app://');
            setIsStandalone(isInStandaloneMode);
        };

        checkStandalone();

        const handler = (e) => {
            e.preventDefault();
            // Don't show install prompt if already installed
            if (!isStandalone) {
                console.log('PWA installation is available');
                setSupportsPWA(true);
                setPromptInstall(e);
                setShowInstallMessage(true);
            }
        };

        window.addEventListener('beforeinstallprompt', handler);

        // Also listen for display mode changes
        const mediaQuery = window.matchMedia('(display-mode: standalone)');
        mediaQuery.addListener(checkStandalone);

        return () => {
            window.removeEventListener('beforeinstallprompt', handler);
            mediaQuery.removeListener(checkStandalone);
        };
    }, [isStandalone]);

    const handleInstall = async (evt) => {
        evt.preventDefault();
        if (!promptInstall) {
            return;
        }
        promptInstall.prompt();
        const { outcome } = await promptInstall.userChoice;
        if (outcome === 'accepted') {
            console.log('User accepted PWA installation');
            setSupportsPWA(false);
            setShowInstallMessage(false);
        } else {
            console.log('User declined PWA installation');
        }
    };

    const handleClose = () => {
        setShowInstallMessage(false);
    };

    if (!supportsPWA || isStandalone) {
        return null;
    }

    return (
        <Snackbar
            open={showInstallMessage}
            autoHideDuration={15000}
            onClose={handleClose}
            message="This app can be installed on your device!"
            action={
                <Button 
                    color="secondary" 
                    size="small" 
                    onClick={handleInstall}
                >
                    Install
                </Button>
            }
            anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'center',
            }}
            style={{
                bottom: '20px'
            }}
        />
    );
}

export default InstallPWA; 