// Convert various date formats to a standardized date string (YYYY-MM-DD)
export const normalizeDate = (dateStr) => {
    if (!dateStr) return null;

    // If it's just a year
    if (/^\d{4}$/.test(dateStr)) {
        return `${dateStr}-01-01`;
    }

    // If it's MM/YYYY
    if (/^\d{2}\/\d{4}$/.test(dateStr)) {
        const [month, year] = dateStr.split('/');
        return `${year}-${month}-01`;
    }

    // If it's DD/MM/YYYY
    if (/^\d{2}\/\d{2}\/\d{4}$/.test(dateStr)) {
        const [day, month, year] = dateStr.split('/');
        return `${year}-${month}-${day}`;
    }

    return null;
};

// Format date for display
export const formatDateForDisplay = (dateStr) => {
    if (!dateStr) return '';

    // If it's just a year
    if (/^\d{4}$/.test(dateStr)) {
        return dateStr;
    }

    // If it's MM/YYYY
    if (/^\d{2}\/\d{4}$/.test(dateStr)) {
        const [month, year] = dateStr.split('/');
        return `${month}/${year}`;
    }

    // If it's DD/MM/YYYY
    if (/^\d{2}\/\d{2}\/\d{4}$/.test(dateStr)) {
        return dateStr;
    }

    return dateStr;
}; 