import React from 'react';
import { Button, Box } from '@mui/material';
import SortIcon from '@mui/icons-material/Sort';
import ArrowUpwardIcon from '@mui/icons-material/ArrowUpward';
import ArrowDownwardIcon from '@mui/icons-material/ArrowDownward';
import styled from '@emotion/styled';

const MobileOnlyBox = styled(Box)`
    display: none;
    @media (max-width: 960px) {
        display: flex;
        gap: 8px;
        padding: 8px;
        justify-content: center;
        background: rgba(255, 255, 255, 0.05);
        margin-bottom: 16px;
    }
`;

const SortButtons = ({ onSort, sortConfig }) => {
    const getArrowIcon = (key) => {
        if (sortConfig.key === key) {
            return sortConfig.direction === 'asc' ? <ArrowUpwardIcon /> : <ArrowDownwardIcon />;
        }
        return <SortIcon />;
    };

    return (
        <MobileOnlyBox>
            <Button 
                variant="outlined" 
                size="small"
                startIcon={getArrowIcon('Date')}
                onClick={() => onSort('date')}
            >
                Sort by Date
            </Button>
            <Button 
                variant="outlined" 
                size="small"
                startIcon={getArrowIcon('Name')}
                onClick={() => onSort('name')}
            >
                Sort by Name
            </Button>
        </MobileOnlyBox>
    );
};

export default SortButtons; 