import React, { memo } from 'react';
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Chip,
  TableSortLabel
} from '@material-ui/core';
import { getAlbumTypeColor } from '../utils/icons';
import FormatIcon from './FormatIcon';
import { formatDateForDisplay } from '../utils/dateUtils';

// Memoize individual rows to prevent unnecessary re-renders
const AlbumRow = memo(({ album, index }) => (
    <TableRow className="album-row">
        <TableCell>{index + 1}</TableCell>
        <TableCell>{album.Name}</TableCell>
        <TableCell>
            {album.Band.map((band, index) => (
                <React.Fragment key={index}>
                    {band}
                    {index < album.Band.length - 1 && <br />}
                </React.Fragment>
            ))}
        </TableCell>
        <TableCell>
            <Chip 
                label={album.AlbType} 
                size="small"
                style={{
                    backgroundColor: getAlbumTypeColor(album.AlbType),
                    color: 'white'
                }}
            />
        </TableCell>
        <TableCell>
            <div className="chip-container">
                {album.Genres.map((genre, idx) => (
                    <Chip 
                        key={idx} 
                        label={genre} 
                        size="small" 
                        className="table-chip"
                    />
                ))}
            </div>
        </TableCell>
        <TableCell>
            <div className="format-icons">
                {album.Format.map((format, idx) => (
                    <FormatIcon 
                        key={idx}
                        format={format}
                        className="table-format-icon"
                    />
                ))}
            </div>
        </TableCell>
        <TableCell>
            {formatDateForDisplay(album.Date) || album.Year}
        </TableCell>
    </TableRow>
));

function AlbumTable({ albums, sortConfig, onSort }) {
    return (
        <TableContainer component={Paper} className="album-table">
            <Table>
                <TableHead>
                    <TableRow>
                        <TableCell>#</TableCell>
                        <TableCell>
                            <TableSortLabel
                                active={sortConfig.key === 'Name'}
                                direction={sortConfig.direction.toLowerCase()}
                                onClick={() => onSort('name')}
                            >
                                Album Name
                            </TableSortLabel>
                        </TableCell>
                        <TableCell>Band/Artist</TableCell>
                        <TableCell></TableCell>
                        <TableCell>Tags</TableCell>
                        <TableCell>Format</TableCell>
                        <TableCell>
                            <TableSortLabel
                                active={sortConfig.key === 'Date'}
                                direction={sortConfig.direction.toLowerCase()}
                                onClick={() => onSort('date')}
                            >
                                Release
                            </TableSortLabel>
                        </TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {albums.map((album, index) => (
                        <AlbumRow 
                            key={album.Name + index} 
                            album={album} 
                            index={index}
                        />
                    ))}
                </TableBody>
            </Table>
        </TableContainer>
    );
}

// Memoize the entire table component
export default memo(AlbumTable); 