import React from 'react';
import { Tooltip } from '@material-ui/core';
import { getFormatIcon } from '../utils/icons';

const FormatIcon = ({ format, className, showTooltip = true }) => {
    const iconPath = getFormatIcon(format);
    const IconComponent = () => (
        <img 
            src={process.env.PUBLIC_URL + iconPath} 
            alt={format}
            className={className}
        />
    );

    if (!showTooltip) return <IconComponent />;

    return (
        <Tooltip title={format} arrow placement="top">
            <span>
                <IconComponent />
            </span>
        </Tooltip>
    );
};

export default FormatIcon; 