import React from 'react';
import { 
  Select, 
  MenuItem, 
  FormControl, 
  InputLabel,
  Paper,
  TextField,
  IconButton,
  Chip
} from '@material-ui/core';
import { Clear } from '@material-ui/icons';

function FilterBar({ selectedFormat, setSelectedFormat, searchTerm, setSearchTerm }) {
  const formats = ['CD', 'Vinyl', 'Cassette', 'Digital', 'VCD', 'DVD', 'Book', 'Reel-To-Reel'];

  const handleClear = () => {
    setSearchTerm('');
    setSelectedFormat([]);
  };

  const handleFormatChange = (event) => {
    setSelectedFormat(event.target.value);
  };

  return (
    <Paper className="filter-bar">
      <div className="search-field-container">
        <TextField
          className="search-field"
          fullWidth
          variant="outlined"
          size="small"
          placeholder="Search by album name, band or genre..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
        />
        {(searchTerm || selectedFormat.length > 0) && (
          <IconButton 
            size="small" 
            className="clear-button"
            onClick={handleClear}
            title="Clear filters"
          >
            <Clear />
          </IconButton>
        )}
      </div>
      <FormControl variant="outlined" size="small" className="format-select">
        <InputLabel>Format</InputLabel>
        <Select
          multiple
          value={selectedFormat}
          onChange={handleFormatChange}
          label="Format"
          renderValue={(selected) => (
            <div className="format-chips">
              {selected.map((value) => (
                <Chip 
                  key={value} 
                  label={value} 
                  size="small"
                  className="format-chip"
                />
              ))}
            </div>
          )}
        >
          {formats.map((format) => (
            <MenuItem key={format} value={format}>
              {format}
            </MenuItem>
          ))}
        </Select>
      </FormControl>
    </Paper>
  );
}

export default FilterBar; 