// Create a mapping object for format icons
export const formatIcons = {
    'CD': '/icons/cd.png',
    'Mini CD': '/icons/mini-cd.png',
    'VHS': '/icons/vhs.png',
    'VCD': '/icons/vcd.png',
    'USB': '/icons/usb.png',
    'Vinyl': '/icons/vinyl.png',
    'Cassette': '/icons/cassette.png',
    'Reel-To-Reel': '/icons/reel-to-reel.png',
    'Digital': '/icons/digital.png',
    'DVD': '/icons/dvd.png',
    'Book': '/icons/book.png',
    'Promotion': '/icons/promotion.png',
    'TV': '/icons/tv.png',
    'Cine': '/icons/cine.png',
    'Unknown': '/icons/unknown.png'
};

// Album type colors
export const albumTypeColors = {
    'Album': '#2563eb',          // Rich Blue
    'Live Session Album': '#2563eb', // Same as Album
    'Live Album': '#2563eb',     // Same as Album
    'EP': '#059669',             // Deep Emerald
    'Live EP': '#059669',        // Same as EP
    'Live Session EP': '#059669', // Same as EP
    'Single': '#dc2626',         // Deep Red
    'Compilation': '#7c3aed',    // Deep Purple
    'Book': '#334155',           // Slate Blue
    'Documentary': '#ea580c',    // Burnt Orange
    'default': '#64748b'         // Medium Slate
};

// Helper functions
export const getFormatIcon = (format) => {
    return formatIcons[format] || formatIcons['Unknown'];
};

export const getAlbumTypeColor = (type) => {
    return albumTypeColors[type] || albumTypeColors.default;
};

