import React, { useState, useEffect, useMemo, useCallback } from 'react';
import { Container, useMediaQuery, useTheme, Box, LinearProgress } from '@material-ui/core';
import FilterBar from './components/FilterBar';
import AlbumTable from './components/AlbumTable';
import AlbumCard from './components/AlbumCard';
import InstallPWA from './components/InstallPWA';
import data from './data.json';
import './styles/App.css';
import { ThemeProvider } from '@material-ui/core/styles';
import SortButtons from './components/SortButtons';
import useScrollDirection from './hooks/useScrollDirection';
import SplashScreen from './components/SplashScreen';
import { useInView } from 'react-intersection-observer';

function App() {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  
  const [albums] = useState(data);
  console.log(albums);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedFormat, setSelectedFormat] = useState([]);
  const [sortConfig, setSortConfig] = useState({ key: 'Date', direction: 'desc' });
  const [isOnline, setIsOnline] = useState(navigator.onLine);
  const isHeaderVisible = useScrollDirection();
  const [loading, setLoading] = useState(true);
  const [visibleItems, setVisibleItems] = useState(10);
  const { ref: loadMoreRef, inView } = useInView();

  useEffect(() => {
    const handleOnline = () => setIsOnline(true);
    const handleOffline = () => setIsOnline(false);

    window.addEventListener('online', handleOnline);
    window.addEventListener('offline', handleOffline);

    return () => {
      window.removeEventListener('online', handleOnline);
      window.removeEventListener('offline', handleOffline);
    };
  }, []);

  useEffect(() => {
    // Simulate loading time
    const timer = setTimeout(() => {
      setLoading(false);
    }, 2000); // Show splash screen for 2 seconds

    return () => clearTimeout(timer);
  }, []);

  const handleRefresh = () => {
    if ('serviceWorker' in navigator) {
      // Unregister service worker to force refresh
      navigator.serviceWorker.ready.then(registration => {
        registration.unregister().then(() => {
          // Clear cache
          caches.keys().then(keys => {
            keys.forEach(key => caches.delete(key));
          });
          // Reload the page
          window.location.reload(true);
        });
      });
    } else {
      // Fallback for browsers without service worker
      window.location.reload(true);
    }
  };

  // Update OfflineIndicator to include refresh button
  const OfflineIndicator = () => (
    <div className={`offline-indicator ${!isOnline ? 'visible' : ''}`}>
      <span>You are currently offline. Some features may be limited.</span>
      <button 
        onClick={handleRefresh}
        style={{
          marginLeft: '10px',
          padding: '4px 8px',
          backgroundColor: '#fff',
          border: 'none',
          borderRadius: '4px',
          cursor: 'pointer'
        }}
      >
        Refresh
      </button>
    </div>
  );

  // Memoize filtered albums to prevent unnecessary recalculations
  const filteredAlbums = useMemo(() => {
    return albums.filter(album => {
      const searchMatch = 
        album.Name.toLowerCase().includes(searchTerm.toLowerCase()) ||
        album.Band.some(band => band.toLowerCase().includes(searchTerm.toLowerCase())) ||
        album.Genres.some(genre => genre.toLowerCase().includes(searchTerm.toLowerCase()));
      
      const formatMatch = selectedFormat.length === 0 || 
        album.Format.some(format => selectedFormat.includes(format));

      return searchMatch && formatMatch;
    });
  }, [albums, searchTerm, selectedFormat]);

  // Memoize sorted albums
  const sortedAlbums = useMemo(() => {
    return [...filteredAlbums].sort((a, b) => {
      if (sortConfig.key === 'Name') {
        return sortConfig.direction === 'asc' 
          ? a.Name.localeCompare(b.Name)
          : b.Name.localeCompare(a.Name);
      }
      if (sortConfig.key === 'Date') {
        const parseDate = (dateStr) => {
          if (!dateStr) return null;
          
          // Handle YYYY format
          if (/^\d{4}$/.test(dateStr)) {
            return new Date(parseInt(dateStr), 0, 1);
          }
          
          // Handle MM/YYYY format
          if (/^\d{2}\/\d{4}$/.test(dateStr)) {
            const [month, year] = dateStr.split('/');
            return new Date(parseInt(year), parseInt(month) - 1, 1);
          }
          
          // Handle DD/MM/YYYY format
          if (/^\d{2}\/\d{2}\/\d{4}$/.test(dateStr)) {
            const [day, month, year] = dateStr.split('/');
            return new Date(parseInt(year), parseInt(month) - 1, parseInt(day));
          }
          
          return null;
        };

        const dateA = parseDate(a.Date) || parseDate(a.Year?.toString());
        const dateB = parseDate(b.Date) || parseDate(b.Year?.toString());
        
        // Handle cases where one or both dates are null
        if (!dateA && !dateB) return 0;
        if (!dateA) return 1;
        if (!dateB) return -1;
        
        return sortConfig.direction === 'asc'
          ? dateA - dateB
          : dateB - dateA;
      }
      return 0;
    });
  }, [filteredAlbums, sortConfig]);

  // Update sortConfig when handleSort is called
  const handleSort = (type) => {
    if (type === 'date') {
      setSortConfig(prev => ({
        key: 'Date',
        direction: prev.key === 'Date' && prev.direction === 'desc' ? 'asc' : 'desc'
      }));
    } else if (type === 'name') {
      setSortConfig(prev => ({
        key: 'Name',
        direction: prev.key === 'Name' && prev.direction === 'desc' ? 'asc' : 'desc'
      }));
    }
  };

  // Reset visible items when filters change
  useEffect(() => {
    setVisibleItems(10);
  }, [searchTerm, selectedFormat, sortConfig]);

  // Load more items when scrolling
  useEffect(() => {
    if (inView) {
      setVisibleItems(prev => Math.min(prev + 10, sortedAlbums.length));
    }
  }, [inView, sortedAlbums.length]);

  // Get visible albums
  const visibleAlbums = useMemo(() => {
    return sortedAlbums.slice(0, visibleItems);
  }, [sortedAlbums, visibleItems]);

  if (loading) {
    return <SplashScreen />;
  }

  return (
    <ThemeProvider theme={theme}>
      <Container maxWidth="lg">
        <OfflineIndicator />
        <Box className={`sticky-controls ${isHeaderVisible ? 'visible' : 'hidden'}`}>
          <FilterBar
            selectedFormat={selectedFormat}
            setSelectedFormat={setSelectedFormat}
            searchTerm={searchTerm}
            setSearchTerm={setSearchTerm}
          />
          <SortButtons 
            onSort={handleSort} 
            sortConfig={sortConfig}
          />
        </Box>
        {isMobile ? (
          <>
            <div className="album-cards">
              {visibleAlbums.map((album, index) => (
                <AlbumCard key={index} album={album} />
              ))}
            </div>
            {visibleItems < sortedAlbums.length && (
              <Box 
                ref={loadMoreRef}
                sx={{ 
                  height: '20px', 
                  margin: '20px 0',
                  display: 'flex',
                  justifyContent: 'center'
                }}
              >
                <LinearProgress 
                  sx={{ 
                    width: '200px',
                    backgroundColor: 'rgba(115, 3, 11, 0.2)',
                    '& .MuiLinearProgress-bar': {
                      backgroundColor: '#73030b'
                    }
                  }} 
                />
              </Box>
            )}
          </>
        ) : (
          <AlbumTable 
            albums={sortedAlbums}
            sortConfig={sortConfig}
            onSort={handleSort}
          />
        )}
        <InstallPWA />
      </Container>
    </ThemeProvider>
  );
}

export default App; 