import React, { useState, memo } from 'react';
import {
  Card,
  CardContent,
  Typography,
  Collapse,
  IconButton,
  Chip,
  Box
} from '@material-ui/core';
import { ExpandMore, ExpandLess, DateRange } from '@material-ui/icons';
import { getAlbumTypeColor, CalendarIcon } from '../utils/icons';
import FormatIcon from './FormatIcon';
import { formatDateForDisplay } from '../utils/dateUtils';

function AlbumCard({ album }) {
  const [expanded, setExpanded] = useState(false);

  return (
    <Card className="album-card">
      <CardContent className="album-card-content">
        <Box className="album-header">
          <Box className="album-title-section">
            <Typography variant="h6" className="album-name" title={album.Name}>
              {album.Name}
            </Typography>
            <Box className="album-meta">
              <Typography variant="subtitle2" className="album-band">
                {album.Band.map((band, index) => (
                  <span key={index} className="band-name">
                    {band}
                  </span>
                ))}
              </Typography>
              <Chip 
                label={album.AlbType}
                size="small"
                className="album-type"
                style={{
                  backgroundColor: getAlbumTypeColor(album.AlbType),
                  color: 'white'
                }}
              />
            </Box>
          </Box>
        </Box>

        <Box className="album-info-section">
          <Box className="album-year-section">
            {CalendarIcon ? (
              <img 
                src={process.env.PUBLIC_URL + CalendarIcon} 
                alt="Calendar" 
                className="calendar-icon" 
              />
            ) : (
              <DateRange fontSize="small" />
            )}
            <Typography variant="body2">
              {formatDateForDisplay(album.Date) || album.Year}
            </Typography>
          </Box>
          
          <Box className="format-section">
            {album.Format.map((format, index) => (
                <FormatIcon 
                    key={index}
                    format={format}
                    className="format-icon"
                />
            ))}
          </Box>
        </Box>

        <Collapse in={expanded} timeout="auto">
          <Box className="album-details">
            <Typography variant="subtitle2" className="section-title">
              Tags
            </Typography>
            <Box className="genre-chips">
              {album.Genres.map((genre, index) => (
                <Chip 
                  key={index}
                  label={genre}
                  size="small"
                  className="genre-chip"
                />
              ))}
            </Box>
          </Box>
        </Collapse>

        <IconButton 
          onClick={() => setExpanded(!expanded)}
          className={`expand-button ${expanded ? 'expanded' : ''}`}
          size="small"
        >
          {expanded ? <ExpandLess /> : <ExpandMore />}
        </IconButton>
      </CardContent>
    </Card>
  );
}

export default memo(AlbumCard); 